<template>
    <div class="module-ur-rss-feed rss-feed-container">
        <ul>
            <li
                    class="article"
                    v-for="(article, index) of arrArticles"
                    :key="index">
                <img
                        :src="arrImgs[index]"
                        class="thumbnail">
                <div class="article-text">
          <span class="title">
            {{ article.title ? article.title[0] : ''}}
          </span>
                    <span class="desc" v-html="article.description ? article.description[0] : ''">
                    </span>
                    <span
                            class="pub-date">
            {{ article.pubDate ? pubDateFilter(article.pubDate[0]) : ''}}
          </span>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
  import axios from 'axios'
  import xml2js from 'xml2js'

  export default {
    name: 'ur-rss-feed',
    data: () => ({
      URL: 'https://www.bfmtv.com/rss/info/flux-rss/flux-toutes-les-actualites/', // ToDO: Use providers to get user's url
      arrArticles: [],
      arrImgs: []
    }),
    mounted() {
      this.getXML()
      setInterval(() => {
        this.getXML()
      }, 1000 * 60 * 5)
    },
    methods: {
      getXML: function () {
        axios.get(this.URL).then(response => {
          xml2js.parseString(response.data, (err, data) => {
            for (const article of data.rss.channel[0].item) {
              this.arrArticles.push(article)
              if (article.enclosure !== undefined) {
                this.arrImgs.push(article.enclosure[0].$.url)
              }
            }
          })
        }).catch(e => {
          if (e) throw e
        })
      },
      pubDateFilter: function (val) {
        const date = new Date(val)
        return `Published on ${`${date.getDate()}`.length === 1 ? `0${date.getDate()}` : date.getDate()}/${`${date.getMonth() + 1}`.length === 1 ? `0${date.getMonth() + 1}` : date.getMonth() + 1}/${date.getFullYear()} at ${`${date.getHours()}`.length === 1 ? `0${date.getHours()}` : date.getHours()}:${`${date.getMinutes()}`.length === 1 ? `0${date.getMinutes()}` : date.getMinutes()}`
      }
    }
  }
</script>

