        align-items: center;
        margin-bottom: 10px;
    }

    img.thumbnail {
        width: 40%;
    }

    .article-text {
        color: white;
        font-family: 'Roboto', sans-serif;
        display: flex;
        flex-direction: column;
        margin-left: 15px;
    }

    span.title {
        font-size: 1.5em;
        font-weight: 400;
        margin-bottom: 20px;
    }

    span.desc {
        font-weight: 100;
        font-size: 1em;
        text-align: justify;
    }

    span.pub-date {
        font-weight: 100;
        font-size: 0.75em;
        text-align: right;
    }
</style>

